import React, { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  Button,
  Container,
  Form,
  FormFeedback,
  FormGroup,
  Input,
  Modal,
  ModalBody,
  ModalHeader,
  Spinner,
} from "reactstrap";
import Base from "../Base";
import { loginUserReducer } from "../reducers/authReducer";
import { ErrorAlert, SuccessAlert } from "../shared-components/AlertBox";

const Login = () => {
  const args = {
    backdrop: false,
    centered: true,
    size: "sm",
    fade: false,
  };

  const dispatch = useDispatch();
  const navigate = useNavigate();
  //const regex = `/^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i`;
  const regex = /^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,}$/;
  const { error, loading, success, userdetails } = useSelector(
    (state) => state.user
  );

  const [formData, setFormData] = useState({
    email: "",
    password: "",
  });

  const [errors, setErrors] = useState({
    email: "",
    mobile: "",
  });

  const [userValid, setUserValid] = useState(null);

  const inputHandler = (e) => {
    if (e.target.id === "mobile" && isNaN(e.target.value)) {
      e.preventDefault();
      return false;
    }
    setFormData({ ...formData, [e.target.id]: e.target.value });
  };

  const validateHandler = (e) => {
    if (formData.email === "") {
      setErrors({ ...errors, email: "" });
      setUserValid(null);
    } else if (formData.email !== "" && regex.test(formData.email) === false) {
      setErrors({ ...errors, email: "Email is not valid" });
      setUserValid("invalid");
    } else {
      setErrors({ ...errors, email: "" });
      setUserValid("valid");
    }
  };

  const token = userdetails ? userdetails.token : null;

  useEffect(() => {
    if (token) {
      navigate("/");
    }
  }, [token]);

  const submitLoginHandler = async () => {
    await dispatch(loginUserReducer(formData));
  };

  return (
    <>
      <Base isFooter="hideFooter">
        <Container>
          <Modal isOpen={true} style={{ pointerEvents: "none" }} {...args}>
            <ModalHeader>Login</ModalHeader>
            <ModalBody>
              <Form>
                <FormGroup>
                  <Input
                    type="text"
                    id="email"
                    placeholder="Enter User Id"
                    valid={userValid && userValid === "valid"}
                    invalid={userValid === "invalid" ? true : false}
                    value={formData.email}
                    onChange={(e) => inputHandler(e)}
                    onBlur={(e) => validateHandler(e)}
                  />
                  <FormFeedback
                    valid={userValid && userValid === "valid"}
                    invalid={userValid === "invalid" ? "invalid" : ""}
                  >
                    {errors.email}
                  </FormFeedback>
                </FormGroup>
                <FormGroup>
                  <Input
                    type="password"
                    id="password"
                    placeholder="Enter Password"
                    onChange={(e) => inputHandler(e)}
                  />
                  <FormFeedback>Incorrect Password</FormFeedback>
                </FormGroup>
              </Form>
              <div className="d-grid py-2">
                <Button
                  className="primary-button"
                  disabled={
                    formData.email === "" ||
                    formData.password === "" ||
                    regex.test(formData.email) === false
                  }
                  onClick={submitLoginHandler}
                >
                  Login
                </Button>
                <Link
                  to="/security"
                  className="small text-decoration-none text-center primary-link p-3"
                >
                  Forgot Password?
                </Link>
                <Link
                  to="/signup"
                  className="small text-decoration-none text-center primary-link"
                >
                  Create Account{" "}
                </Link>
              </div>
            </ModalBody>
            {/* <ModalFooter className='flex-column'>
          
        </ModalFooter> */}
            {loading && (
              <span className="position-absolute top-50 start-50 translate-middle">
                <Spinner size="sm" color="primary">
                  Loading...
                </Spinner>
              </span>
            )}
          </Modal>
        </Container>
        {error && <ErrorAlert message={error} />}
        {success && <SuccessAlert message={success} />}
      </Base>
    </>
  );
};

export default Login;
